//    
export const getLogged = () => {
    return !!localStorage.getItem("token");
}


//    
export const excludeProp = (props, ...exludeArr) => {

    //  
    let copyProps = Object.assign({}, props);

    //   (   )
    exludeArr.map((exlude) => {

        //   exclude
        delete copyProps[exlude];
    });
    return copyProps;
}


